export class User {
  id: number;
  // 
  username: string;
  email: string;
  userAvatarUrl: string;
  // 
  totalCharacters: string;  //  service  k  m
  totalReading: string;
  totalLikes: string;
  // 
  introduction: string;
  field: string;

  constructor (id:number, username: string, email: string, userAvatarUrl: string,
               totalCharacters: string, totalReading: string, totalLikes: string,
               introduction: string, field: string) {
    this.id = id;
    this.username = username;
    this.email = email;
    this.userAvatarUrl = userAvatarUrl;
    // 
    this.totalCharacters = totalCharacters;  //  service  k  m
    this.totalReading = totalReading;
    this.totalLikes = totalLikes;
    // 
    this.introduction = introduction;
    this.field = field;
  }
}



