import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import { Article, Tag, Comment } from './article';
import 'rxjs/add/operator/map';

@Injectable()
export class ArticleService {
  // 
  rawArticles: Article[] = [
    {
      id: 0,
      date: '2016  8  16 ',
      image: null,
      title: ' ',
      synopsis: ' ',
      tagName: '',
      contents: '## Vahana\nVahana  6  2020 \n## Cormorant\nCormorant  Urban Aeronautics  115 ( 185 )\n```C\n#include "stdio.h"\n\nint main() {\n\treturn 0;\n}\n```',
      comments: []
    },
    {
      id: 1,
      date: '2017  2  6 ',
      image: '.././assets/images/article1.jpeg',
      title: ' ',
      synopsis: '',
      tagName: '',
      contents: '## Vahana\nVahana  6  2020 \n## Cormorant\nCormorant  Urban Aeronautics  115 ( 185 )\n```C\n#include "stdio.h"\n\nint main() {\n\treturn 0;\n}\n```',
      comments: []
    },
    {
      id: 2,
      date: '2016  6  3 ',
      image: '.././assets/images/article3.jpg',
      title: ' 135 ',
      synopsis: '',
      tagName: '',
      contents: '## Vahana\nVahana  6  2020 \n## Cormorant\nCormorant  Urban Aeronautics  115 ( 185 )\n```C\n#include "stdio.h"\n\nint main() {\n\treturn 0;\n}\n```',
      comments: []
    }
  ];
  // 
  rawComments: Comment[] = [
    {
      username: '',
      userAvatarUrl: '.././assets/images/user-avatar2.jpg',
      message: '',
      time: '2 ',
      likes: 16
    },
    {
      username: 'An0nym6',
      userAvatarUrl: '.././assets/images/user-avatar.jpg',
      message: 'Cormorant  Urban Aeronautics  115 ( 185 )',
      time: '1 ',
      likes: 7
    }
  ];

  //  service 
  //  raw data 
  articles: Article[] = [];

  //  tags 
  tags: Tag[] = [];

  constructor(private http: Http) {
    let i: number;
    for (i = 0; i < 77; i++) {
      // 
      var newArticle = new Article(i, this.rawArticles[i % 3].date, this.rawArticles[i % 3].image,
                                   this.rawArticles[i % 3].title, this.rawArticles[i % 3].synopsis,
                                   this.rawArticles[i % 3].tagName, this.rawArticles[i % 3].contents,
                                   this.rawComments);
      this.articles.push(newArticle);
    }
  }

  // 
  // TODO 
  getArticles(pageNum: number) {
    pageNum--;
    if (pageNum < 0 || pageNum > Math.ceil(this.articles.length / 10))
      return null;
    return this.articles.slice(pageNum * 10, pageNum * 10 + 10);
  }

  //  10 
  getMaxPageNumber() {
    return Math.ceil(this.articles.length / 10);
  }

  // 
  getArticle(id: number) {
    return this.articles[id];
  }

  // 
  getPopularArticles() {
    return this.articles.slice(0, 5);
  }

  // 
  getTags() {
    // 
    for (let article of this.articles) {
      let isExist: boolean = false;
      for (let tag of this.tags)
        if (tag.tagName == article.tagName) {
          tag.number++;
          isExist = true;
          break;
        }
      if (!isExist)
        this.tags.push(new Tag(article.tagName, 1));
    }
    this.tags.sort();
    return this.tags;
  }
}



